export default [

  {
    path: '/performances/',
    component: require('./pages/performances.vue')
  },
	{ path: '/performance/:id',
      component: require('./pages/performance.vue')
    },
	{ path: '/piece/:id',
      component: require('./pages/piece.vue')
    },
	{ path: '/before/:id',
      component: require('./pages/before.vue')
    },
	{ path: '/during/:id',
      component: require('./pages/during.vue')
    },
	{ path: '/after/:id',
      component: require('./pages/after.vue')
    },
	{ path: '/specials/',
      component: require('./pages/specials.vue')
    },
	{ path: '/sponsors/',
      component: require('./pages/sponsors.vue')
    },
    {
      path: '/',
		component: require('./app.vue')
    },
	{
		path: '/account',
        name: 'account',
        component: require('./components/Account.vue'),
        meta: {auth: true}
	}
]


