<template>
  <f7-page hide-bars-on-scroll id="during" name="during">
  <f7-navbar>
			  <f7-nav-left :back-link="piece.people_name" sliding class="text-uppercase"></f7-nav-left>
			  <f7-nav-center></f7-nav-center>
			   <f7-nav-right class="navbar-title text-uppercase highlight">
				{{performance.performance_feeling}}

			  </f7-nav-right>
			</f7-navbar>

    <div id="line"></div>
	<div id="live-feed">
<f7-grid no-gutter id="live-feed-container">

  <transition-group name="feed-items" mode="out-in" appear>
  <f7-col v-for="item in items" :key="item.id" class="feed-item">
    <p>{{item.content}}</p>
  <!--<p class="time regular">posted <span v-text="timePosted(item.time_published, performance.performance_time)"></span></p>-->
<p class="like"><i class="icon f7-icons">heart</i></p>
  </f7-col>
   </transition-group>
</f7-grid>
</div>
  </f7-page>
</template>

<script>
import axios from 'axios'
import moment from 'moment'


export default {
  data () {
    return {
	  id: this.$route.params.id,
	  performance_id: null,
	  performance: {},
      piece: {},
	  items: [],
    itemBox: '#live-feed'
    }
  },
  created () {

    let id = this.$route.params.id
	 axios.get(`https://huestudios.com/sites/philharmonic/api/pieces.php?id=${id}`).then(response => {
      this.piece = response.data[0];
		this.performance_id = this.piece.performance_id;
	  axios.get(`https://huestudios.com/sites/philharmonic/api/performance.php?id=`+this.performance_id).then(response => {
			this.performance = response.data[0]
		}).catch(function (error) {
			console.log(error)
		})
    }).catch(function (error) {
      console.log(error)
    });
	
	setInterval(function () {
      this.loadItems();
      this.checkConcertMode();
    }.bind(this), 500);
  },
  
  methods: {
    imgPath (img) {
      return 'https://huestudios.com/sites/philharmonic/app/images/performances/small/sm_' + img
    },
	loadItems() {
      axios.get(`https://huestudios.com/sites/philharmonic/api/live_feed.php?id=`+ this.performance_id).then(response => {
   		  this.items = response.data
        document.getElementById("live-feed").className += " visible";
      }).catch(function (error) {
        console.log(error)
      });

  },
	timePosted(time, start) {
		var newTime = moment(time).format('h:mm:ss A');
		var newStart = moment(start).format('h:mm:ss A');
		return moment(newTime, "h:mm:ss").startOf(newStart).fromNow();
	},
	checkConcertMode: function () {
      var concertMode; axios.get(`https://huestudios.com/sites/philharmonic/api/settings.php`).then(response => {
          concertMode = response.data[0].concert_mode
       }).catch(function (error) {
          console.log(error)
       }).then(function(){
		if(concertMode == '1') {
		  document.getElementById("live-feed").className += " hideOverflow";
		
		} else {
		  document.getElementById('live-feed').classList.remove("hideOverflow");
		}
	  });
	},
	

  }
}
</script>

<style lang="scss">
@import '../css/variables';
@function my-calculation-function($some-number, $another-number){
  @return $some-number - $another-number
}
#line {
	position: fixed;
	top: 0px;
	left: 50%;
	margin-left: -1px;
	width: 2px;
	height: 100vh;
	background: rgb(0,0,0);
	z-index: 1;
}
#live-feed {
	position: fixed;
	bottom: 0%;
	left: 0px;
	width: 100%;
	height: 100vh;
	overflow: scroll;
	z-index: 5;
  opacity: 0;

  #live-feed-container {
		align-items: flex-end;
  		justify-content: center;
		height: auto;
         padding-top: 25vh;
		 position: relative;
	}


 .feed-item {
 	list-style-type: none;
	background: rgb(25,25,25);
	border-radius: 50%;
	border: thin solid #000;
	//box-shadow: 0px 0px 15px #444;
	margin: 10px 0 80px;
	width: 350px;
	height: 350px;
	padding:0px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: rgba(0,0,0, 1);
	transition: all 1.2s cubic-bezier(0.23, 1, 0.32, 1);
	position:relative;
	p {
			text-align: center;
	font-size: 17px;
	line-height: 25px;
	padding: 0 10px;
	}
	.time {
	  font-size: 10px;
	  opacity: 0.5;
	}
	.like {
		position: absolute;
		bottom: 30px;
		left: 50%;
		font-size: 16px;
		margin-left: -8px;
		padding: 0px;
	}
 }
 .feed-item:first-of-type {
 color: rgba($white, 1.0);
 border: thin solid #999;
	
 }
}

#live-feed.visible {
  opacity: 1;
}

.feed-items-move {
  transition: all 1.2s cubic-bezier(0.23, 1, 0.32, 1);
}

.feed-items-enter, .feed-items-leave-to {
  transform: translateY(-200px);
  opacity: 0;
}

.feed-items-leave-active {
 	position: absolute;
}
#live-feed.hideOverflow {
  overflow:hidden;
}
</style>



