<script>
import axios from 'axios'


export default {
  name: 'app',
  data () {
    return {
		context: 'app context',
        loaded: false,
	  date: this.formatDate(new Date()),
	  first_name: '',
	  last_name: '',
	  email: 'peter@huestudios.com',
	  password: 'p195pr',
	  concert_mode: false,
	  screen_takeover: false,
	  screen_takeover_message: '',
	  performance: ''
    }
  },
  created () {
 axios.get(`https://huestudios.com/sites/philharmonic/api/performance.php`).then(response => {
      this.performance = response.data[0]

    }).catch(function (error) {
      console.log(error)
    });
	 setInterval(function () {
          this.checkSettings();
      }.bind(this), 1000);
  },
  methods: {
    formatDate: function (date) {
	  var monthNames = [
		"January", "February", "March",
		"April", "May", "June", "July",
		"August", "September", "October",
		"November", "December"
	  ];

	  var day = date.getDate();
	  var monthIndex = date.getMonth();
	  var year = date.getFullYear();

	  return monthNames[monthIndex] + ' ' + day + ', ' + year;
	},
	checkSettings: function () {
      var screenTakeOver; axios.get(`https://huestudios.com/sites/philharmonic/api/settings.php`).then(response => {
      this.concert_mode = response.data[0].concert_mode
	  screenTakeOver = response.data[0].screen_takeover
	  this.screen_takeover_message = response.data[0].screen_takeover_message
	   this.screen_takeover = screenTakeOver
    	}).catch(function (error) {
      console.log(error)
    }).then(function(){
		if(screenTakeOver == '1') {
		  document.getElementById('screen-takeover-btn').click();
		} else {
		  document.getElementById('close-screen-takeover').click();
		}
		
	});
	},
	 login: function () {
      let user = {
              email: 'peter@huestudios.com', 
              password: 'p195pr'
            };

            if (this.$auth.isAuthenticated()) {
              this.$auth.logout()  
            }

            this.$auth.login(user).then(function (response) {
              this.response = response

              console.log(this.response)
            })
    },
	register: function() {
            let user = {
              first_name: 'John',
			  last_name: 'Tester',
              email: 'john.doe@domain.com', 
              password: 'pass123456'
            };

            if (this.$auth.isAuthenticated()) {
              this.$auth.logout()  
            }
            
            this.$auth.register(user).then(function (response) {
              this.response = response

              console.log(this.$auth.isAuthenticated())
            })
          },

       logout: function() {
            this.$auth.logout().then(() => {
              if (!this.$auth.isAuthenticated()) {
                this.response = null
              }
            })
          }
  }
}

</script>
