<template>
  <f7-page hide-bars-on-scroll id="performance" name="performance">
  <f7-navbar>
			  <f7-nav-left back-link="BACK" sliding></f7-nav-left>
			  <f7-nav-center></f7-nav-center>
			   <f7-nav-right class="navbar-title text-uppercase">
				{{performance.performance_feeling}}
			  </f7-nav-right>
			</f7-navbar>
    <f7-block no-hairlines class="performance">

    <h1 class="text-uppercase">{{performance.performance_title}}</h1>
	
<h4 class="text-uppercase" v-bind:style="`color:` + performance.performance_color">{{ performance.performance_date | moment("dddd, MMMM Do YYYY") }}</h4>

	<p>{{performance.performance_description}}</p>
<!--<img class="sponsor-logo" v-bind:src=extrasImgPath(sponsor.image) :alt="sponsor.extra_title" v-if="sponsor.image"> -->
  <p id="sponsor-text" class="text-uppercase condensed" v-if="sponsor.extra_title">Proudly supported by <span class="condensed-bold">{{sponsor.extra_title}}</span></p>
  <f7-list no-hairlines>
	<f7-list-item v-for="piece in pieces" :key="piece.piece_id" v-bind:link="`/piece/` + piece.piece_id" class="arrow"><h2 class="text-uppercase">{{piece.people_name}}</h2> <svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg></f7-list-item>
	</f7-list>
	  
    </f7-block>
 <f7-block inner no-hairlines class="icon-navbar">
<f7-grid>
  <f7-col width="20" tablet-width="15">
  <a href=""><h5 class="text-uppercase">WISHLIST</h5><i class="icon f7-icons">list</i></a>
  </f7-col>
  <f7-col width="20" tablet-width="15">
   <f7-link v-bind:href="performance.ticket_url" external><h5 class="text-uppercase">TICKETS</h5><i class="icon f7-icons">ticket</i></f7-link>
   </f7-col>
  <f7-col width="20" tablet-width="15">
  <a href=""><h5 class="text-uppercase">RATE</h5><i class="icon f7-icons">star</i></a>
   </f7-col>
  <f7-col width="20" tablet-width="15">
  <f7-link open-popup="#share-popup"><h5 class="text-uppercase">SHARE</h5><i class="icon f7-icons">share</i></f7-link>
   </f7-col>
  <f7-col width="20" tablet-width="15">
  <f7-link v-bind:href="'http://maps.google.com?q=' +performance.extra_address" external><h5 class="text-uppercase">VENUE</h5><i class="icon f7-icons">navigation</i></f7-link>
  </f7-col>
  </f7-grid>
  </f7-block>
  </f7-page>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      id: null,
      performance: {},
	  pieces: [],
	  sponsor: ''
    }
  },
  created () {
    let id = this.$route.params.id
    axios.get(`https://huestudios.com/sites/philharmonic/api/performance.php?id=${id}`).then(response => {
      this.performance = response.data[0]
	  this.titleSponsor(this.performance.title_sponsor)
    }).catch(function (error) {
      console.log(error)
    }).then(function (){
	});
	 axios.get(`https://huestudios.com/sites/philharmonic/api/pieces.php?performance_id=${id}`).then(response => {
      this.pieces = response.data
    }).catch(function (error) {
      console.log(error)
    })
  },
  methods: {
    imgPath (img) {
      return 'https://huestudios.com/sites/philharmonic/app/images/performances/small/sm_' + img
    },
	extrasImgPath (img) {
      return 'https://huestudios.com/sites/philharmonic/app/images/extras/' + img
    },
	titleSponsor: function (performanceID) {
		axios.get(`https://huestudios.com/sites/philharmonic/api/sponsors.php?id=`+performanceID).then(response => {
          this.sponsor = response.data[0]
        }).catch(function (error) {
           console.log(error)
        })
	},
  }
}
</script>
<style lang="scss">
@import '../css/variables';
.performance {
padding: 0;
	
  h1 {
   font-size: 32px;
		line-height: 32px;
		margin: 75px 0 5px;
		padding: 0;
		padding: 0 40px;
  @media (min-width: $medium) {
  margin: 90px 0 5px;
				font-size: 64px;
				line-height: 64px;
				}
 
  }
  p {
  padding: 0 40px;
  margin: 5px 0;
  font-size: 16px;
  }
  h4 {
  		font-size: 20px;
		line-height: 20px;
		margin: 5px 0 5px;
		padding: 0 40px;
		@media (min-width: $medium) {
				font-size: 28px;
				line-height: 28px;
				}
		
  }
  .list-block {
  margin-bottom: 15px;
  ul {
	border-top: 1px solid #666;
	border-bottom: 1px solid #666;
	}
	}
		.arrow {
			.item-content {
			padding-left: 0;
			}
			.item-inner {
				padding: 40px 40px;
				background-image: none !important;
				justify-content: space-between !important;
 
	
  h2 {
		 font-size: 28px;
		line-height: 28px;
		margin: 20px 0 20px;
		padding: 0;
		@media (min-width: $medium) {
				font-size: 42px;
				line-height: 42px;
				}
		}
		
}
}
}
.icon-navbar {
margin: 0px 0 60px;
	text-align: center;
	.row {
	justify-content: center;
	}
	a {
		h5 {
		font-size: 12px;
		margin-bottom: 8px;
		}
		i {
			border-radius: 50%;
			border: thin solid #ffffff;
			width: 35px;
			height: 35px;
			font-size: 20px;
			line-height: 35px;
		}
	}
}
#sponsor-text {
text-align: right;
color: #999999;
 img {
// filter: grayscale(100%);
 width: 70px;
 height: auto;
 }
 span {
  color: #ffffff;
  font-weight: 900;
 }
}
</style>


