<template>

  <f7-page hide-bars-on-scroll id="performances" name="performances">
  		<f7-navbar>
			  <f7-nav-left back-link="HOME" sliding></f7-nav-left>
			  <f7-nav-center></f7-nav-center>
			   <f7-nav-right class="navbar-title" sliding>
				SEASON
			  </f7-nav-right>
			</f7-navbar>
    <f7-block no-hairlines class="performances">
	<f7-list>
	<f7-list-item class="arrow" v-for="performance in performances" :key="performance.performance_id" v-bind:link="`/performance/` + performance.performance_id">
<f7-grid no-gutter>
  <f7-col width="90">
	<h1 class="text-uppercase">{{performance.performance_title}}</h1>
	
	<h2 class="text-uppercase" v-bind:style="`color:` + performance.performance_color">{{ performance.performance_date | moment("dddd, MMMM Do YYYY") }} <!--{{ performance.performance_time | moment("hh:mm A") }}--></h2>
	<h3 class="text-uppercase">{{performance.extra_title}}</h3>
	</f7-col>
	<f7-col width="10">
	 <svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg>
	 </f7-col>
	 </f7-grid>
	</f7-list-item>
	</f7-list>
   </f7-block>

  </f7-page>
  
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      performances: []
    }
  },
  created () {
    axios.get(`https://huestudios.com/sites/philharmonic/api/performances.php`).then(response => {
      this.performances = response.data
      console.log(response)
    }).catch(function (error) {
      console.log(error)
    })
  },
  methods: {
    imgPath (img) {
      return 'https://huestudios.com/sites/philharmonic/app/images/performances/small/sm_' + img
    }
  }
}
</script>
<style lang="scss">
@import '../css/variables';
#performances {
.performances{
	padding: 0;
		.arrow {
			.item-content {
			padding-left: 0;
			}
			.row {
			width: 100%;
			align-items: center;
			}
			.item-inner {
				padding: 40px 20px;
				background-image: none !important;
				justify-content: space-between !important;
					@media (min-width: $medium) {
				padding: 50px 40px;
				}

				h1 {
				font-size: 32px;
				line-height: 32px;
				margin: 0px 0 5px;
				padding: 0;
				@media (min-width: $medium) {
				font-size: 64px;
				line-height: 64px;
				}
				}
				h2 {
				font-size: 20px;
				line-height: 20px;
				margin: 5px 0 8px;
				padding: 0;
				@media (min-width: $medium) {
				font-size: 28px;
				line-height: 28px;
				}
				}
				h3 {
					font-size: 16px;
				line-height: 16px;
				margin: 5px 0 0 0;
				padding: 0;
				opacity: 0.5;
				@media (min-width: $medium) {
				font-size: 18px;
				line-height: 18px;
				}
				}
				.description {
				margin-top: 10px;
				text-align: right;

				}
				.arrow-icon {
				height: 40px;
					width: auto;
					padding-left: 0px;
				@media (min-width: $medium) {
					height: 90px;
					width: auto;
					padding-left: 50px;
					}
					line {
						fill: none;
						stroke: $white;
						stroke-width: 2px;
					}
				}
			}
		}
	}

}
</style>


