<template>
  <f7-page id="piece" name="piece">
  <f7-navbar>
			  <f7-nav-left :back-link="performance.performance_title" class="text-uppercase " sliding></f7-nav-left>
			  <f7-nav-center></f7-nav-center>
			   <f7-nav-right class="navbar-title text-uppercase highlight">
				{{performance.performance_feeling}}
			  </f7-nav-right>
			</f7-navbar>
    <f7-block no-hairlines class="piece">
	
	<h1 class="text-uppercase">{{piece.people_name}}</h1>
    <h4 class="text-uppercase">{{piece.piece_title}}</h4>
	  <f7-list>
	<f7-list-item v-bind:link="`/before/` + piece.piece_id" class="arrow">
	<f7-grid no-gutter>
  <f7-col width="90">
  <h2 class="text-uppercase">Learn <span class="lighter">/ Before</span></h2>
	<p>Learn about the origin, historical context, style, era, musician highlights and more.</p>
	</f7-col>
	 <f7-col width="10">
	<svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg>
	</f7-col>
	</f7-grid>
	</f7-list-item>
	<f7-list-item v-bind:link="`/during/` + piece.piece_id" class="arrow">
	<f7-grid no-gutter>
  <f7-col width="90"><h2 class="text-uppercase">Follow <span class="lighter">/ During</span></h2>
	
	<p>Anticipate highlighted movements and important moments throughout the piece.</p>
	</f7-col>
  <f7-col width="10">
	<svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg>
	</f7-col>
	</f7-grid></f7-list-item>
	<f7-list-item v-bind:link="`/after/` + piece.piece_id" class="arrow">
	<f7-grid no-gutter>
  <f7-col width="90"><h2 class="text-uppercase">Celebrate <span class="lighter">/ After</span></h2>
	
	<p>Relive the show, discover what to do after, tell us your thoughts, and share your experience.</p>
	</f7-col>
	</f7-col>
  <f7-col width="10">
	<svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg>
	</f7-col>
	</f7-grid></f7-list-item>
	</f7-list>

  
  

    </f7-block>

  </f7-page>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      id: null,
	  performance_id: null,
      piece: {},
	  performance: {}
    }
  },
  created () {
    let id = this.$route.params.id
	
	 axios.get(`https://huestudios.com/sites/philharmonic/api/pieces.php?id=${id}`).then(response => {
      this.piece = response.data[0]
      this.performance_id = this.piece.performance_id;
	  axios.get(`https://huestudios.com/sites/philharmonic/api/performance.php?id=`+this.performance_id).then(response => {
			this.performance = response.data[0]
			console.log(this.performance.performance_feeling)
		}).catch(function (error) {
			console.log(error)
		})
    }).catch(function (error) {
      console.log(error)
    })
	
  },
  methods: {
  }
}
</script>

<style lang="scss">
@import '../css/variables';
.piece {
padding: 0;
	
  h1 {
  		font-size: 32px;
		line-height: 32px;
		margin: 75px 0 5px;
		padding: 0;
		padding: 0 40px;
		 @media (min-width: $medium) {
				font-size: 64px;
				line-height: 64px;
				margin: 90px 0 5px;
				}
  }
  h4 {
  		font-size: 20px;
		line-height: 20px;
		margin: 5px 0 5px;
		padding: 0 40px;
		@media (min-width: $medium) {
				font-size: 28px;
				line-height: 28px;
				}
		
		
  }
  .list-block {
  margin-bottom: 15px;
  ul {
	border-top: 1px solid #666;
	border-bottom: 1px solid #666;
	}
	}
  .arrow {
  .row {
			width: 100%;
			align-items: center;
			}
			.item-content {
			padding-left: 0;
			}
			.item-inner {
				padding: 40px 40px;
				background-image: none !important;
				justify-content: space-between !important;
 
	
  h2 {
		 font-size: 28px;
		line-height: 28px;
		margin: 20px 0 20px;
		padding: 0;
			@media (min-width: $medium) {
				font-size: 42px;
				line-height: 42px;
				}
		}
		.lighter {
		opacity: 0.3;
		}
		
}
}
}
</style>


