<template>
  <f7-page id="before" name="before">
   <f7-navbar>
			  <f7-nav-left :back-link="piece.people_name" sliding class="text-uppercase"></f7-nav-left>
			  <f7-nav-center></f7-nav-center>
			   <f7-nav-right class="navbar-title text-uppercase" sliding>
				{{genre.extra_title}}
			  </f7-nav-right>
			</f7-navbar>
	<f7-block no-hairlines class="before">
			 <h1 class="text-uppercase">{{piece.piece_title}}</h1>
	<f7-accordion>
  <!-- Item 1 -->
  <f7-accordion-item>
    <f7-accordion-toggle class="arrow"><h2>ORIGIN</h2><svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg></f7-accordion-toggle>
    <f7-accordion-content>
	 <h3 class="text-uppercase" v-if="genre.extra_title">{{genre.extra_title}}</h3>
  <p v-if="genre.extra_description"><img class="header-image" v-bind:src="extrasImgPath(genre.image)">{{genre.extra_description}}</p>
    </f7-accordion-content>
  </f7-accordion-item>
  <!-- Item 2 -->
  <f7-accordion-item>
    <f7-accordion-toggle class="arrow"><h2 class="text-uppercase">Composer</h2><svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg></f7-accordion-toggle>
    <f7-accordion-content>
     <h3 class="text-uppercase" v-if="composer.people_name">{{composer.people_name}}</h3>
  <p><img class="header-image" v-bind:src="peopleImgPath (composer.image)">{{composer.people_bio}}</p>
    </f7-accordion-content>
  </f7-accordion-item>
    <!-- Item 2 -->
  <f7-accordion-item>
    <f7-accordion-toggle class="arrow"><h2>PERFORMERS</h2> <svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg></f7-accordion-toggle>
    <f7-accordion-content>
     <f7-block class="performers">
   <f7-accordion>
   <f7-accordion-item>
   <f7-accordion-toggle class="text-uppercase">
   <div v-if="conductor.image" class="header-image" v-bind:style="`background-image: url(http://huestudios.com/sites/philharmonic/app/images/people/small/sm_` + conductor.image  + `);`"></div>
  <h3 v-if="conductor.people_name" class="text-uppercase">{{conductor.people_name}}</h3>
  <h4>{{conductor.people_title}}</h4>
	 <i class="icon f7-icons">chevron-down</i>
	</f7-accordion-toggle>
    <f7-accordion-content>
  <p v-if="conductor.people_bio">{{conductor.people_bio}}</p>
 </f7-accordion-content>
  </f7-accordion-item>
  <f7-accordion-item v-for="guest in guests" :key="guest.people_id">
    <f7-accordion-toggle class="text-uppercase musician">
	<div class="header-image" v-bind:style="`background-image: url(http://huestudios.com/sites/philharmonic/app/images/people/small/sm_` + guest.image  + `);`"></div>
	
	 <h3 v-if="guest.people_name">{{guest.people_name}}</h3> <h4 v-if="guest.people_instrument"><span class="" v-if="guest.people_title">{{guest.people_title}} / </span> {{guest.people_instrument}}</h4>
	
	 <i class="icon f7-icons">chevron-down</i>
	</f7-accordion-toggle>
    <f7-accordion-content>
      <p v-if="guest.people_bio">{{guest.people_bio}}</p>
	  <p v-else>Bio coming soon.</p>
    </f7-accordion-content>
  </f7-accordion-item>
  <f7-accordion-item v-for="musician in musicians" :key="musician.people_id">
    <f7-accordion-toggle class="text-uppercase musician">
	<div class="header-image" v-bind:style="`background-image: url(http://huestudios.com/sites/philharmonic/app/images/people/small/sm_` + musician.image  + `);`"></div>
	
	 <h3 v-if="musician.people_name">{{musician.people_name}}</h3> <h4 v-if="musician.people_instrument"><span class="" v-if="musician.people_title">{{musician.people_title}} / </span> {{musician.people_instrument}}</h4>
	
	 <i class="icon f7-icons">chevron-down</i>
	</f7-accordion-toggle>
    <f7-accordion-content>
      <p v-if="musician.people_bio">{{musician.people_bio}}</p>
	  <p v-else>Bio coming soon.</p>
    </f7-accordion-content>
  </f7-accordion-item>
  
</f7-accordion>
   </f7-block>
    </f7-accordion-content>
  </f7-accordion-item>
    <!-- Item 2 -->
  <f7-accordion-item>
    <f7-accordion-toggle class="arrow"><h2 class="text-uppercase">STORY</h2><svg class="arrow-icon" data-name="arrow-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 27.08 130.55"><title>Arrow Icon</title><line  x1="0.46" y1="0.19" x2="26.62" y2="65.37"/><line x1="26.62" y1="65.19" x2="0.46" y2="130.37"/></svg></f7-accordion-toggle>
    <f7-accordion-content>
    <h3 class="text-uppercase" v-if="piece.piece_title">{{piece.piece_title}}</h3>
  <p><img class="header-image" v-if="piece.image" v-bind:src="imgPath(piece.image)">{{piece.piece_story}}</p>
    </f7-accordion-content>
  </f7-accordion-item>
</f7-accordion>
  
    </f7-block>

  </f7-page>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      id: null,
      piece: {},
	  genreId: null,
	  composerId: null,
	  conductorId: null,
	  genre: '',
	  composer: '',
	 conductor: '',
	 musicians: [],
	 guests: []
    }
  },
  created () {
     let id = this.$route.params.id
	 axios.get(`https://huestudios.com/sites/philharmonic/api/pieces.php?id=${id}`).then(response => {
      this.piece = response.data[0]
	  this.genreId = this.piece.genre_id;
	  this.composerId = this.piece.composer_id;
	  this.conductorId = this.piece.conductor_id;	  
	  	axios.get(`https://huestudios.com/sites/philharmonic/api/extras.php?id=` + this.genreId).then(response => {
		  this.genre = response.data[0]
		}).catch(function (error) {
		  console.log(error)
		});
		 axios.get(`https://huestudios.com/sites/philharmonic/api/person.php?id=` + this.composerId).then(response => {
      		this.composer = response.data[0]
    	}).catch(function (error) {
      		console.log(error)
    	});
		axios.get(`https://huestudios.com/sites/philharmonic/api/person.php?id=` + this.conductorId).then(response => {
		  this.conductor = response.data[0]
		}).catch(function (error) {
		  console.log(error)
		});
		
    }).catch(function (error) {
      console.log(error)
    });
	axios.get(`https://huestudios.com/sites/philharmonic/api/people.php?id=${id}`).then(response => {
		this.musicians = response.data;
	}).catch(function (error) {
		 console.log(error)
	});
	axios.get(`https://huestudios.com/sites/philharmonic/api/guests.php?id=${id}`).then(response => {
		this.guests = response.data;
	}).catch(function (error) {
		 console.log(error)
	});
	
  },
  methods: {
    imgPath (img) {
      return 'http://huestudios.com/sites/philharmonic/app/images/performances/' + img
    },
	peopleImgPath (img) {
      return 'http://huestudios.com/sites/philharmonic/app/images/people/small/sm_' + img
    },
	extrasImgPath (img) {
      return 'http://huestudios.com/sites/philharmonic/app/images/extras/' + img
    }
  }
}
</script>
<style lang="scss">
@import '../css/variables';
.before {
padding: 0;
 h1 {
  font-size: 28px;
		line-height: 28px;
		margin: 75px 0 45px;
		padding: 0;
		padding: 0 40px;
		opacity: 0.5;
			@media (min-width: $medium) {
				font-size: 42px;
				line-height: 42px;
				margin: 135px 0 45px;
				}
  }
  .accordion-item:nth-of-type(1) {
  border-top: thin solid #333;
  }
.accordion-item {
	border-bottom: thin solid #333;
}
.arrow {
display: flex;
justify-content: space-between !important;
	align-items: center;
		padding: 40px;			
 
	
  h2 {
		font-size: 32px;
		line-height: 32px;
		margin: 0;
			@media (min-width: $medium) {
				font-size: 64px;
				line-height: 64px;
				}
		}
		.arrow-icon {
		margin-right: 10px;
height: 40px;
width: auto;
transform-origin: center center;
transition: all 0.15s linear;
@media (min-width: $medium) {
margin-right: 50px;
height: 60px;
}
line {
fill: none;
stroke: $white;
stroke-width: 2px;
}
}
		
}
.accordion-item-toggle {
transition: all 0.15s linear;
 h2 {
 transition: all 0.15s linear;
 }
}
.accordion-item-expanded {
		.accordion-item-toggle {
		background: $black2;
		h2 {
			opacity: 0.3;
		}
		
		}
		.arrow-icon {
			transform: rotate(90deg) translateX(10px);
		}
		.accordion-item-content {
			h3 {
				
			}
		}
	}

.accordion-item-content {
	background: $black2;
	padding: 0 40px;
	h3 {
	font-size: 28px;
	line-height: 28px;
	padding: 0;
	margin: 0;
	transition: all 0.15s linear;
	}
	.header-image {
	float: right;
	width: 100%;
	height: auto;
	margin: 0 0 30px 0px;
	@media (min-width: $medium) {
	width: 270px;
	margin: 0 0 30px 30px;
	}
	}
	p {
	font-size: 16px;
	text-align: justify;
	padding-bottom: 40px;
	}
}



.performers {
padding: 0;
.accordion-list {
background: $black2;
}
.accordion-item{
margin: 40px 0;

}
.accordion-item:nth-of-type(1) {
  border-top: none;
  }
 .header-image {
	width: 60px;
	height: 60px;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	
	position: relative;
	float: left;
	margin-right: 20px;
	margin-left: 0px;
	margin-bottom: 0px;
	}
	.accordion-item-toggle {
	
	position: relative;
	h3 {
	 margin: 0;
	 line-height: 1em;
	 font-size: 32px;
	}
	h4 {
	margin: 0;
	 line-height: 1em;
	 opacity: 0.7;
	 font-size: 18px;
	 margin-top: 10px;
	 margin-bottom: 30px;
	span {
		opacity: 0.5;
	}
	}
	i {
	position: absolute;
	right: 5px;
	top: 50%;
	margin-top: -10px;
	font-size: 20px;
	line-height: 20px;
	}
	}
	.accordion-item-content {
	background: $black2;
	padding: 0 0px;
	}
	.accordion-item-expanded {
		.accordion-item-toggle {
		background: $black2;
		h2 {
			opacity: 1;
		}
		
		}
		.arrow-icon {
			transform: rotate(90deg);
		}
		.accordion-item-content {
			h3 {
				
			}
		}
	}
}
}
</style>


