<template>
  <f7-page name="sponsors">
  <f7-navbar>
			  <f7-nav-left back-link="BACK" sliding></f7-nav-left>
			  <f7-nav-center></f7-nav-center>
			   <f7-nav-right class="navbar-title text-uppercase" sliding>
				SPONSORS</f7-nav-right>
			</f7-navbar>
    <f7-block no-hairlines class="sponsors">
	<f7-grid>
	<f7-col v-for="sponsor in sponsors" :key="sponsor.extra_id" v-if="sponsor.image" width="50" tablet-width="33" :data-rank="sponsor.extra_rank">
	<f7-link :href="sponsor.extra_url" target="_blank" external><img class="sponsor-logo" v-bind:src=imgPath(sponsor.image)></f7-link>
	</f7-col>
	</f7-grid>

</f7-block>


  </f7-page>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      id: null,
      sponsors: {}
    }
  },
  created () {	 axios.get(`https://huestudios.com/sites/philharmonic/api/sponsors.php`).then(response => {
      this.sponsors = response.data
      console.log(this.sponsors)
    }).catch(function (error) {
      console.log(error)
    })
  },
  methods: {
    imgPath (img) {
      return 'https://huestudios.com/sites/philharmonic/app/images/extras/' + img
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../css/variables';
.sponsors {
background: $white;

.row {
align-items: center;
justify-content: space-around;
}
}
.sponsor-logo {
width: 90%;
height: auto;
text-align: center;
margin: 10px 5%;
background: $white;
}
</style>


